<template>
	<div class="loginbox">
		<div class="leftBg"></div><div class="rightBg"></div>
		<div class="login_main">
			<div class="loginform">
				<div class="login-left">
				<h3><span>注册</span></h3>
					<form>
						<div class="form_item">
							<ul>
							   <li>
							   		<Icon type="person" class="itemicon"></Icon><Input placeholder="请输入登录账号" clearable class="forminput"></Input>
							   </li>
							   <li>
									<Icon type="ios-unlocked" class="itemicon"></Icon></Icon><Input placeholder="请输入登录密码" type="password" clearable class="forminput"></Input>
							   </li>
							   <li>
									<Icon type="ios-unlocked" class="itemicon"></Icon></Icon><Input placeholder="请输入确认密码" type="password" clearable class="forminput"></Input>
							   </li>
							   <li style="text-align:left">
									<Icon type="ios-unlocked" class="itemicon"></Icon></Icon><Input placeholder="请输入登录密码" type="password" clearable class="forminput" style="width:260px"></Input><img v-bind:src="codeimgsrc" class="codeimg">
							   </li>
							   <li>
									<Button type="primary"  @click="regaction">注册</Button>
							   </li>
							</ul>
						</div>
					</form>
					<div class="form_item other-login">
						<ul>
						   <li>已有技术库账号？<router-link to="/login">立即登录</router-link></li>
						</ul>
					</div>
				</div>
				<div class="login-right">

				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
  data () {
    return {
      codeimgsrc: 'sdfsdf'
    }
  },
  methods: {
    regaction () {
      setTimeout(() => {
        this.$router.push({ path: '/' })
      }, 3000)
    }
  }
}
</script>

<style>
html, body {
    width: 100%;
    height: 100%;
    background: #f0f0f0;
    overflow: hidden;
    font-weight:300;
}
</style>


